<template>
	<div
		:class="[
			'dropdown',
			{
				show,
			}
		]"
	>
		<!-- v-on="inputEvents" -->
		<a
			class="btn btn-outline-secondary dropdown-toggle"
			href="javascript:void(0)"
			role="button"
			data-toggle="dropdown"
			aria-haspopup="true"
			:aria-expanded="show"
			@click="toggleMenu"
		>
			<!-- id="dropdownMenuLink" -->
			<i v-if="!noDot" class="m-r-10 mdi mdi-dots-horizontal" />
			<span v-text="title"/>
		</a>
		<div
			:class="[
				'dropdown-menu',
				{
					show,
				}
			]"
			aria-labelledby="dropdownMenuLink"
			@click.stop="1"
		>
			<template
				v-for="({ text, type, action }, index) in items"
			>
				<a
					v-if="text"
					v-text="text"
					:class="`dropdown-${type || 'c'}`"
					href="javascript:void(0)"
					:key="index"
					@click.stop="$emit('onMenu', { text, type, index, action })"
				/>
				<div v-else class="dropdown-divider" :key="index" />
			</template>
		</div>
	</div>
</template>

