export default {
	name: 'dropdown-menu',
	props: {
		title: {
			type: [String, Number],
			default: 'title',
		},
		items: {
			type: [Array, Object],
			default() {
				return []
			},
		},
		noDot: {
			default: false,
		},
	},
    data() {
        return {
			show: false,
        }
    },
    components: {
        
    },
    methods: {
		toggleMenu() {
			this.show = !this.show;
			const { body } = document;
			if (this.show) {
				setTimeout(() => {
					body.addEventListener('click', this.toggleMenu)
				}, 0);
			} else {
				body.removeEventListener('click', this.toggleMenu)
			}
		},
