<template>
  <div>
    <!-- Modal -->
    <modal
      :popup="showModal"
      :title="modalTitle"
      :cancel="+modalType !== 1"
      v-bind="{
        ...(+modalType === 2
          ? { classBody, }
          : {})
      }"
      :button-c="+modalType === 2 ? '儲存' : '確定'"
      @finish="closeMod()"
      @confirm="onConfirm"
    >
      <p
        v-if="+modalType < 2"
        v-text="modalMessage"
      />
      <div
        v-else
        class="modal-body"
      >
        <div
          v-for="item in inputList"
          :key="item.title"
          class="form-group row"
        >
          <label
            for="lname"
            class="col-sm-2 text-right control-label col-form-label"
            v-text="item.title"
          />
          <div class="col-sm-10">
            <input
              v-if="item.type === 'input'"
              v-model="item.value"
              type="text"
              class="form-control"
              :placeholder="item.placeholder"
            >
            <textarea
              v-else
              v-model="item.value"
              class="form-control"
              style="height:100px;"
              :placeholder="item.placeholder"
            />
          </div>
        </div>
        <li
          v-if="alertMessage"
          style="color: #F00"
          v-text="alertMessage"
        />
      </div>
    </modal>
    <!-- Modal -->

    <div
      id="MessageModal"
      class="modal fade"
      tabindex="-1"
      data-keyboard="false"
      data-backdrop="static"
      role="dialog"
      aria-labelledby="exampleModalLabel"
      aria-hidden="true"
    >
      <div
        class="modal-dialog modal-mid modal-dialog-centered"
        role="document"
      >
        <div class="modal-content">
          <div class="modal-header">
            <h5
              id="MessageModalLabel"
              class="modal-title"
            >
              系統提示
            </h5>
            <button
              type="button"
              class="close"
              data-dismiss="modal"
              aria-label="Close"
            >
              <span aria-hidden="true">&times;</span>
            </button>
          </div>
          <div
            class="modal-body"
            style="text-align: center;font-size:16px"
          />
          <div class="modal-footer">
            <button
              type="button"
              class="btn btn-success"
              style="width:85px;"
              data-dismiss="modal"
            >
              確定
            </button>
          </div>
        </div>
      </div>
    </div>

    <div class="page-wrapper">
      <div class="page-breadcrumb">
        <div class="row">
          <div class="col-12 d-flex no-block align-items-center">
            <h4 class="page-title">
              幣別匯率設定
            </h4>
          </div>
        </div>
      </div>
      <div class="container-fluid">
        <div class="card border-bottom">
          <div
            class="card-body"
            style=""
          >
            <div
              id="ShipTypeDiv"
              class="row mb-3"
            >
              <div
                class="col-md-12"
                style="padding: 8px 0px 0px 0px;"
              >
                <div id="NormalMode">
                  <button
                    type="button"
                    class="btn btn-outline-danger"
                    @click="callModal({
                      message: '',
                      title: '新增貨幣',
                      action: 'onCreate',
                      type: 2,
                    })"
                  >
                    <i
                      class="mdi mdi-plus"
                      style="font-style: normal;"
                    > 新增貨幣</i>
                  </button>
                  <button
                    type="button"
                    class="btn btn-outline-secondary"
                    @click="callModal({
                      message: '是否將所選項目改為「正常使用」？',
                      action: 'onEnableAll',
                    })"
                  >
                    <i
                      class="mdi mdi-arrow-up-bold-circle"
                      style="font-style: normal;"
                    > 正常使用</i>
                  </button>
                  <button
                    type="button"
                    class="btn btn-outline-secondary"
                    @click="callModal({
                      message: '是否將所選項目改為「停止使用」？',
                      action: 'onStopAll',
                    })"
                  >
                    <i
                      class="mdi mdi-arrow-down-bold-circle-outline"
                      style="font-style: normal;"
                    > 停止使用</i>
                  </button>
                  <button
                    type="button"
                    class="btn btn-outline-warning"
                    @click="callModal({
                      message: '是否刪除所選的項目？',
                      action: 'onDeleteAll',
                    })"
                  >
                    <i
                      class="mdi mdi-delete"
                      style="font-style: normal;"
                    > 刪除</i>
                  </button>
                </div>
              </div>
            </div>
          </div>
        </div>
        <data-table
          :titles="[
            '貨幣名稱',
            '代碼',
            '匯率',
            '其他資訊',
          ]"
          :pagination="pagination"
          @onPage="onPage"
          @check="onAllCheck"
        >
          <tr
            v-for="({ CurrencyName, CurrencyNo, Rate, Status, Remark, Id }, key) in list"
            :key="key"
          >
            <td>
              <label class="customcheckbox m-b-20">
                <input
                  ref="checkbox"
                  type="checkbox"
                  @input="onCheck({ $event, Id, key })"
                >
                <span class="checkmark" />
              </label>
            </td>
            <td>
              <li>
                <a
                  href="javascript:void(0)"
                  class="product-spec"
                  v-text="CurrencyName"
                />
              </li>
              <li
                v-if="Status"
                style="color: #009688"
              >
                正常使用 <i class="m-r-10 mdi mdi-check" />
              </li>
              <li
                v-else
                style="color: #acacac"
              >
                停用
              </li>
            </td>
            <td v-text="CurrencyNo" />
            <td v-text="Rate" />
            <td>
              <dropdown
                :title="'操作'"
                :items="[
                  { text: '修改', type: 'item', action: 'edit' },
                  { type: 'divider' },
                  { text: Status ? '停止使用' : '啟用', type: 'item' },
                  { text: '刪除', type: 'item', action: 'delete' },
                ]"
                @onMenu="onMenu({ ...$event, Id, Status, key, })"
              />
              <hr>
              備註：<br>{{ Remark || '(無備註)' }}
            </td>
          </tr>
        </data-table>
      </div>
    </div>
    <div
      v-if="showModal"
      class="modal-backdrop fade show"
    />
  </div>
</template>

<script>
import dataTable from '@/components/dataTable.vue';
import dropdown from '@/components/dropdown.vue';
import modal from '@/components/modal.vue';
import addCurrency from '@/components/addCurrency.vue';
import { mapActions, mapGetters } from 'vuex';

export default {
  name: 'Currencysetting',
  components: {
    dataTable,
    dropdown,
    modal,
    addCurrency,
  },
  data() {
    return {
      modifyingId: '',
      // -- modal --
      showModal: false,
      modalTitle: '',
      modalMessage: '',
      modalType: 0, // 1: 只有確認 2: 填單
      // -- modal --
      alertMessage: '',
      classBody: {
        'modal-dialog': true,
        'modal-900': true,
      },
      checkedList: [],
      inputList: [
        { title: '*貨幣名稱', placeholder: '請輸入貨幣中文名稱', value: '', type: 'input' },
        { title: '*貨幣代碼', placeholder: '請輸入貨幣代碼，例如 RMB或NTD', value: '', type: 'input' },
        { title: '*匯率', placeholder: '該貨幣1元換算成台幣數量', value: '', type: 'input' },
        { title: '備註', placeholder: '', value: '', type: 'textarea' },
      ],
    };
  },
  computed: {
    ...mapGetters({
      list: 'ExchangeRate/list',
      pagination: 'ExchangeRate/pagination',
    }),
  },
  created() {},
  mounted() {
    console.log('debug: mounted -> window.cs = this', window.cs = this);
    this.getList({
      ...this.pagination,
    });
  },
  methods: {
    ...mapActions({
      getList: 'ExchangeRate/read',
      addCurrency: 'ExchangeRate/create',
      changeSetting: 'ExchangeRate/modify',
      changeStatus: 'ExchangeRate/update',
      deleteCurrency: 'ExchangeRate/deleted',
    }),
    callModal({ title, type, message, action }) {
      document.body.classList.add('modal-open');
      document.body.style.paddingRight = '17px';
      this.showModal = true;
      this.modalTitle = title || '提示訊息';
      this.modalMessage = message || '';
      this.modalType = type || 0;
      this.action = action || 'closeMod';
      this.alertMessage = '';
    },
    onConfirm() {
      const action = this[this.action];
      if (typeof action === 'function') {
        action();
      }
    },
    closeMod() {
      document.body.classList.remove('modal-open');
      document.body.style.paddingRight = null;
      this.showModal = false;
      this.modalMessage = '';
      this.alertMessage = '';
      this.inputList.forEach((input) => {
        input.value = '';
      });
    },
    onCreate() {
      const { inputList } = this;

      const CurrencyName = inputList[0].value;
      const CurrencyNo = inputList[1].value;
      const Rate = inputList[2].value;
      const Remark = inputList[3].value;

      if (CurrencyName && CurrencyNo && Rate) {
        // if (true) {
        this.addCurrency({
          CurrencyNo,
          CurrencyName,
          Rate,
          Status: true,
          Remark,
        })
          .then((result) => {
            this.closeMod();
            this.callModal({
              message: '新增成功',
              type: 1,
            });
            // this.modalMessage = '新增成功';
            this.getList({
              ...this.pagination,
            });
          })
          .catch((err) => {
            this.alertMessage = err.message || '不明錯誤';
          });
      } else {
        this.alertMessage = '所有星號(*)必填';
      }
    },
    onEdit() {
      const { inputList } = this;

      const CurrencyName = inputList[0].value;
      const CurrencyNo = inputList[1].value;
      const Rate = inputList[2].value;
      const Remark = inputList[3].value;

      if (CurrencyName && CurrencyNo && Rate) {
        // if (true) {
        this.changeSetting({
          Id: this.modifyingId,
          data: {
            CurrencyNo,
            CurrencyName,
            Rate,
            Remark,
          },
        })
          .then((result) => {
            this.closeMod();
            this.callModal({
              message: '修改成功',
              type: 1,
            });
            // this.modalMessage = '新增成功';
            this.getList({
              ...this.pagination,
            });
          })
          .catch((err) => {
            this.alertMessage = err.message || '不明錯誤';
          });
      } else {
        this.alertMessage = '所有星號(*)必填';
      }
    },
    onMenu({ text, type, index, action, Id, Status, key }) {
      if (action === 'edit') {
        const { CurrencyName, CurrencyNo, Rate, Remark } = this.list[key];
        this.inputList[0].value = CurrencyName;
        this.inputList[1].value = CurrencyNo;
        this.inputList[2].value = Rate;
        this.inputList[3].value = Remark;
        this.callModal({
          message: '',
          title: '修改貨幣',
          action: 'onEdit',
          type: 2,
        });
        this.modifyingId = Id;
      } else if (action === 'delete') {
        this.deleteCurrency(Id)
          .then((result) => {
            this.callModal({
              message: '刪除成功',
              type: 1,
            });
            this.getList({
              ...this.pagination,
            });
          })
          .catch((err) => {
            this.callModal({
              message: err.message || '不明錯誤',
              type: 1,
            });
          });
      } else if (+index === 2) {
        this.changeStatus({
          Id,
          Status: !Status,
        })
          .then((result) => {
            this.callModal({
              message: '修改成功',
              type: 1,
            });
            this.getList({
              ...this.pagination,
            });
          })
          .catch((err) => {
            this.callModal({
              message: err.message || '不明錯誤',
              type: 1,
            });
          });
      }
    },
    onCheck({ $event, Id, key }) {
      const { checked } = $event.target;
      console.log('debug: onCheck -> checked', checked);
      if (checked && !this.checkedList.includes(Id)) {
        this.checkedList.push(Id);
      }
      if (!checked && this.checkedList.includes(Id)) {
        const index = this.checkedList.indexOf(Id);
        // console.log('debug: onCheck -> index', index);
        if (index > -1) {
          this.checkedList.splice(index, 1);
        }
      }
    },
    onAllCheck(checked) {
      if (checked) {
        const { checkbox } = this.$refs;
        const list = Array.isArray(checkbox)
          ? checkbox
          : [checkbox];
        list.forEach((el) => {
          el.checked = true;
        });
        this.checkedList.splice(0);
        this.list.forEach(({ Id }) => {
          this.checkedList.push(Id);
        });
      } else {
        this.clearAllchecked();
      }
    },
    clearAllchecked() {
      const { checkbox } = this.$refs;
      const list = Array.isArray(checkbox)
        ? checkbox
        : [checkbox];
      list.forEach((el) => {
        el.checked = false;
      });
      this.checkedList.splice(0);
    },
    onEnableAll() {
      if (this.checkedList.length) {
        const promiseList = [];
        this.checkedList.forEach((Id) => {
          promiseList.push(this.changeStatus({
            Id,
            Status: true,
          }));
        });
        Promise.allSettled(promiseList)
          .then((result) => {
            // this.closeMod();
            this.callModal({
              message: '修改成功',
              type: 1,
            });
            // this.modalMessage = '修改成功';
            this.getList({
              ...this.pagination,
            });
          })
          .catch((err) => {
            this.callModal({
              message: err.message || '不明錯誤',
              type: 1,
            });
            // this.modalMessage = err.message || '不明錯誤';
          });
      }
    },
    onStopAll() {
      if (this.checkedList.length) {
        const promiseList = [];
        this.checkedList.forEach((Id) => {
          promiseList.push(this.changeStatus({
            Id,
            Status: false,
          }));
        });
        Promise.allSettled(promiseList)
          .then((result) => {
            // this.clearAllchecked();
            // this.closeMod();
            this.callModal({
              message: '修改成功',
              type: 1,
            });
            // this.modalMessage = '修改成功';
            this.getList({
              ...this.pagination,
            });
          })
          .catch((err) => {
            // this.clearAllchecked();
            this.callModal({
              message: err.message || '不明錯誤',
              type: 1,
            });
            // this.modalMessage = err.message || '不明錯誤';
          });
      }
    },
    onDeleteAll() {
      if (this.checkedList.length) {
        const promiseList = [];
        this.checkedList.forEach((Id) => {
          promiseList.push(this.deleteCurrency(Id));
        });
        Promise.allSettled(promiseList)
          .then((result) => {
            this.clearAllchecked();
            // this.closeMod();
            this.callModal({
              message: '刪除成功',
              type: 1,
            });
            // this.modalMessage = '修改成功';
            this.getList({
              ...this.pagination,
            });
          })
          .catch((err) => {
            this.clearAllchecked();
            this.callModal({
              message: err.message || '不明錯誤',
              type: 1,
            });
            // this.modalMessage = err.message || '不明錯誤';
          });
      }
    },
    onPage(Page) {
      if (+Page !== +this.pagination.Page) {
        this.getList({
          ...this.pagination,
          Page,
        });
        this.clearAllchecked();
      }
    },
  },
};

</script>

